import React from 'react';
import { GoogleLogin } from 'react-google-login';
import secret from './secert.json'

export const GoogleSignIn: React.FC = (response: any) => {
    const [userProfile, setUserProfile] = React.useState<any>({
        userName: '',
        userAvatar: ''
    });
    const secretID = secret.web.client_id;

    console.log(secretID);





    const responseGoogle = (response: any) => {

        if (response.profileObj !== undefined) {
            localStorage.setItem('googleSignInName', JSON.stringify(response.profileObj.name));
            localStorage.setItem('googleSignInImageUrl', JSON.stringify(response.profileObj.imageUrl));
            setUserProfile({
                userName: response.profileObj.name,
                userAvatar: response.profileObj.imageUrl,
            })

        }

        // let getGoogleSignIn = localStorage.getItem('googleSignIn');
        // if (getGoogleSignIn !== null) {
        //     getGoogleSignIn = JSON.parse(getGoogleSignIn);
        //     setUserProfile({
        //         userName: getGoogleSignIn.name,
        //         userAvatar: getGoogleSignIn.imageUrl
        //     })


        // console.log(userProfile);

    }


    return (
        <>

            <GoogleLogin
                clientId={secretID}
                onSuccess={responseGoogle}
                onFailure={responseGoogle}
                cookiePolicy={'single_host_origin'}
            >
                <span>Войти через Google</span>
            </GoogleLogin>
            <h3> Вы вошли как {userProfile.userName}
            </h3>
            <img src={userProfile.userAvatar} />

        </>

    );
}